/* ===============================================
   ACCESSIBILITY IMPROVEMENTS
   =============================================== */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.focus-visible {
  outline: 2px solid var(--primary);
  outline-offset: 2px;
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  .stat-card,
  .action-section,
  .overview-stat {
    border-width: 2px;
  }

  .overview-stat .stat-value {
    border-width: 2px;
    font-weight: 800;
  }

  .overview-stat .stat-icon {
    border-width: 2px;
  }

  .variance-indicator {
    border-width: 2px;
    font-weight: 800;
  }

  .overview-stat.actual-performance {
    background: #000000 !important;
    border: 3px solid #ffffff !important;
  }

  .overview-stat.actual-performance .stat-value,
  .overview-stat.actual-performance .stat-label,
  .overview-stat.actual-performance .variance-indicator {
    background: #ffffff !important;
    color: #000000 !important;
    border: 2px solid #000000 !important;
    text-shadow: none !important;
  }
}

/* Reduced motion support */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }

  .stat-card:hover,
  .action-section:hover,
  .overview-stat:hover {
    transform: none;
  }
}

/* ===============================================
   FOCUS STATES FOR ACCESSIBILITY
   =============================================== */
.overview-stat:focus-within {
  outline: 3px solid rgba(79, 70, 229, 0.5);
  outline-offset: 2px;
}

.stat-value:focus-within {
  outline: 2px solid rgba(255, 255, 255, 0.5);
