  color: var(--text-secondary);
}
/* ===============================================
   ENHANCED LINKS PAGE EMPTY STATE STYLES
   =============================================== */

/* Enhanced Empty State */
.empty-state {
  background: linear-gradient(135deg, 
    rgba(102, 126, 234, 0.03) 0%, 
    rgba(118, 75, 162, 0.03) 50%, 
    rgba(255, 255, 255, 0.95) 100%);
  backdrop-filter: blur(20px);
  border-radius: 24px;
  padding: 4rem 3rem;
  text-align: center;
  margin: 2rem 0;
  box-shadow: 
    0 32px 64px rgba(0, 0, 0, 0.12),
    0 0 0 1px rgba(255, 255, 255, 0.05);
  position: relative;
  overflow: hidden;
}

/* Animated background elements */
.empty-state::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle at center, 
    rgba(102, 126, 234, 0.08) 0%, 
    transparent 50%);
  animation: floatingBackground 20s ease-in-out infinite;
  pointer-events: none;
}

.empty-state::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, 
    rgba(255, 255, 255, 0.1) 25%, 
    transparent 25%, 
    transparent 50%, 
    rgba(255, 255, 255, 0.1) 50%, 
    rgba(255, 255, 255, 0.1) 75%, 
    transparent 75%);
  background-size: 40px 40px;
  opacity: 0.03;
  pointer-events: none;
}

@keyframes floatingBackground {
  0%, 100% { transform: translate(0, 0) rotate(0deg); }
  25% { transform: translate(-5%, -5%) rotate(1deg); }
  50% { transform: translate(5%, -10%) rotate(-1deg); }
  75% { transform: translate(-10%, 5%) rotate(1deg); }
}

.empty-content {
  position: relative;
  z-index: 2;
}

/* Enhanced icon with animation */
.empty-content .empty-icon {
  font-size: 5rem;
  margin-bottom: 2rem;
  display: inline-block;
  animation: rocketFloat 6s ease-in-out infinite;
  filter: drop-shadow(0 8px 16px rgba(102, 126, 234, 0.3));
}

@keyframes rocketFloat {
  0%, 100% { 
    transform: translateY(0px) rotate(0deg);
  }
  25% { 
    transform: translateY(-10px) rotate(2deg);
  }
  50% { 
    transform: translateY(-20px) rotate(0deg);
  }
  75% { 
    transform: translateY(-10px) rotate(-2deg);
  }
}

.empty-content h2 {
  font-size: 2.75rem;
  font-weight: 800;
  margin-bottom: 1.5rem;
  background: linear-gradient(135deg, #2c3e50 0%, #667eea 50%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  letter-spacing: -0.02em;
  line-height: 1.2;
}

.empty-content p {
    font-size: 1.1rem;
    color: white !important;
    text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
    margin-bottom: 2rem;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
}
/* Make Income page description text white specifically */
h1:contains("💵 Income Management") ~ .page-description,
.page-title:contains("Income Management") ~ .page-description {
  color: white !important;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}

/* Make only Income page description text white */
.income-page-header .page-description {
  color: white !important;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}
/* Updated page header layout - text left, button right */
.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  text-align: left; /* Override the center alignment */
}

.page-header-content {
  flex: 1;
}

.page-header .page-title {
  font-size: 2rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
}

.page-header .page-description {
  color: var(--text-secondary);
  font-size: 1rem;
  margin: 0;
}

.page-header-actions {
  flex-shrink: 0;
  margin-left: 2rem;
}

/* Responsive: Stack on mobile */
@media (max-width: 768px) {
  .page-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }
  
  .page-header-actions {
    margin-left: 0;
    align-self: stretch;
  }
  
  .page-header-actions .print-button {
    width: 100%;
  }
}
/* Keep other page descriptions dark */
.page-description {
  color: var(--text-secondary);
}

/* Enhanced action button */
.empty-actions {
  margin-bottom: 4rem;
}

.empty-actions .btn {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border: none;
  padding: 1rem 2.5rem;
  font-size: 1.125rem;
  font-weight: 600;
  border-radius: 50px;
  color: white;
  text-transform: none;
  letter-spacing: 0.5px;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 
    0 8px 32px rgba(102, 126, 234, 0.4),
    0 0 0 0 rgba(102, 126, 234, 0.5);
  position: relative;
  overflow: hidden;
}

.empty-actions .btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}

.empty-actions .btn:hover {
  transform: translateY(-3px) scale(1.02);
  box-shadow: 
    0 16px 40px rgba(102, 126, 234, 0.5),
    0 0 0 4px rgba(102, 126, 234, 0.1);
  background: linear-gradient(135deg, #5a67d8 0%, #6b46c1 100%);
}

.empty-actions .btn:hover::before {
  left: 100%;
}

.empty-actions .btn:active {
  transform: translateY(-1px) scale(1.01);
}

/* Enhanced Suggested Categories Section */
.suggested-categories {
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(10px);
  border-radius: 20px;
  padding: 2.5rem;
  border: 1px solid rgba(255, 255, 255, 0.3);
  position: relative;
  z-index: 2;
}

.suggested-categories h4 {
  font-size: 1.75rem;
  font-weight: 700;
  color: #2d3748;
  margin-bottom: 1rem;
  text-align: center;
  position: relative;
}

.suggested-categories h4::after {
  content: '';
  position: absolute;
  bottom: -0.5rem;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 3px;
  background: linear-gradient(90deg, #667eea, #764ba2);
  border-radius: 2px;
}

/* Enhanced suggestions grid */
.suggestions-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 1.5rem;
  margin-top: 2.5rem;
}

/* Enhanced suggestion cards */
.suggestion-card {
  background: linear-gradient(135deg, 
    rgba(255, 255, 255, 0.9) 0%, 
    rgba(255, 255, 255, 0.7) 100%);
  border: 2px solid transparent;
  border-radius: 16px;
  padding: 1.75rem;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  text-align: left;
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(10px);
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.08),
    0 0 0 1px rgba(255, 255, 255, 0.1);
}

.suggestion-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, 
    var(--suggestion-color, #667eea) 0%, 
    var(--suggestion-color-light, #764ba2) 100%);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.suggestion-card:hover::before {
  opacity: 1;
}

.suggestion-card:hover {
  transform: translateY(-8px) scale(1.02);
  border-color: rgba(102, 126, 234, 0.3);
  background: linear-gradient(135deg, 
    rgba(255, 255, 255, 0.95) 0%, 
    rgba(255, 255, 255, 0.85) 100%);
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.15),
    0 0 0 1px rgba(102, 126, 234, 0.1);
}

.suggestion-card:active {
  transform: translateY(-4px) scale(1.01);
}

/* Enhanced suggestion header */
.suggestion-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
}

.suggestion-icon {
  font-size: 2rem;
  flex-shrink: 0;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background: linear-gradient(135deg, 
    rgba(102, 126, 234, 0.1) 0%, 
    rgba(118, 75, 162, 0.1) 100%);
  transition: all 0.3s ease;
}

.suggestion-card:hover .suggestion-icon {
  background: linear-gradient(135deg, 
    rgba(102, 126, 234, 0.2) 0%, 
    rgba(118, 75, 162, 0.2) 100%);
  transform: scale(1.1);
}

.suggestion-name {
  font-size: 1.25rem;
  font-weight: 700;
  color: #2d3748;
  flex: 1;
  line-height: 1.3;
}

/* Enhanced suggestion examples */
.suggestion-examples {
  font-size: 0.9rem;
  color: #64748b;
  line-height: 1.5;
  padding-left: 0.5rem;
  border-left: 3px solid rgba(102, 126, 234, 0.2);
  background: rgba(102, 126, 234, 0.02);
  padding: 0.75rem;
  border-radius: 8px;
  font-style: italic;
}

/* Individual category color customization */
